.questions{background-color:#f6fbe9;border:1px solid #e5f5bd;border-radius:.75rem;height:fit-content;margin:auto;max-width:640px;padding:.625rem 1.875rem;width:100%}@media screen and (min-width: 1024px){.questions{margin:0;max-width:none;padding:1.875rem 3.125rem}}@media screen and (min-width: 1600px){.questions{padding:2.5rem 3.75rem}}.questions__item{border-bottom:1px solid #e5f5bd}.questions__item:last-child{border-bottom:none}.questions__item[open] summary::after{content:"";font-size:1.375rem}.questions__title{cursor:pointer;font-size:1rem;font-weight:600;line-height:1.5;padding:1.25rem 3.5rem 1.25rem 0;position:relative;list-style:none}@media screen and (min-width: 768px){.questions__title{font-size:1.125rem}}@media screen and (min-width: 1600px){.questions__title{font-size:1.25rem}}.questions__title::after{align-items:center;background-color:#234338;border-radius:.5rem;color:#fff;content:"+";display:flex;font-size:2rem;font-weight:500;height:40px;justify-content:center;position:absolute;right:0;top:50%;transform:translateY(-50%);width:40px}.questions__answer{font-size:.875rem;line-height:1.5;padding-bottom:1.75rem}@media screen and (min-width: 768px){.questions__answer{font-size:1rem}}@media screen and (min-width: 1600px){.questions__answer{font-size:1.125rem}}